import React, { useEffect, useState } from 'react'
import './style.css'
import { Sociallogin } from '../../../../utils/actions'
import { useAuth } from '../../../../store/authStore';
import { userstore } from '../../../../store/userStore';
import Cookie from 'components/Common/Cookies';
import { useHistory, useLocation } from 'react-router-dom';
import { userActiveLevelDetailsStore } from '../../../../store/ActiveLevelDetailsStore';
import { useColourObjectStore } from '../../../../store/ColourStore';
import McInput from 'components/Common/McInput';
import { useActiveScreen } from '../../../../store/ActiveScreen';
import { useActivePopup } from '../../../../store/ActivePopup';
import SignupEmailPopup from '../SignupEmailPopup';

const LoginEmailPopup = (props: any) => {
  const history = useHistory()
  const search = useLocation().search;
  const { colourObj } = useColourObjectStore();
  const { setActiveScreen } = useActiveScreen();
  const { setActivePopup } = useActivePopup();
  const { auth, setAuth, logout } = useAuth()
  const { setselfuser } = userstore()
  const [google_cookies, setgoogle_cookies] = useState('')
  const [email_signup, setEmail_signup] = useState(false)
  const [passclear, setPassclear] = useState(false)
  const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();

  interface object1 {
    value: any
    error: any
  }
  const [username_email_or_phone, setusername_email_or_phone] =
    useState<object1>({
      value: '',
      error: false,
    })
  const [password_otp, setpassword_otp] = useState<object1>({
    value: '',
    error: false,
  })

  const handleKeyPress = (event: { key: string }) => {
    if (event.key === 'Enter') {
      emailLogin()
    }
  }

  const emailLogin = () => {
    let data = {
      email_or_username: username_email_or_phone.value,
      password: password_otp.value,
      auth_provider: 'mc',
    }

    Sociallogin(loginCallback, data)
  }

  const loginCallback = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      console.log('response ' + JSON.stringify(data));
      localStorage.setItem('AuthToken', JSON.stringify(data.data.results.token));
      localStorage.setItem('UserDetails', JSON.stringify(data.data.results.user_details));
      localStorage.setItem('UsedSocialLoginMethod', "true");
      setAuth(data.data.results.token)
      setselfuser(data.data.results.user_details)

      const id = new URLSearchParams(search).get('id');
      if (id) {
        loadActiveleveldetailsbyqr(id)
        setActiveScreen("Level")
      }
      else {
        setActiveScreen("Home")
      }
      setActivePopup("")
    } else {
      console.log('error ' + JSON.stringify(data));
      console.log('error:: ' + JSON.stringify(errorresponse));
    };
  }

  return (
    <>
      {!email_signup && (
        <div className='popupformcontainer'>
          <div className="login_button_container">
            <McInput
              // label={'Email Address'}
              id={'email_data'}
              type={'text'}
              inputtype={'email'}
              value={username_email_or_phone?.value}
              onchange={(val: any) => {
                setusername_email_or_phone({
                  value: val,
                  error: '',
                })
              }}
            placeholder={'Enter Email...'}
            />
            {console.log("username_email_or_phone", username_email_or_phone)}
          </div>

          <div className="login_button_container">
            <McInput
              // label={'Password'}
              id={'password_data'}
              type={'password'}
              value={password_otp?.value}
              onchange={(val: any) => {
                setpassword_otp({
                  value: val,
                  error: '',
                })
                // if (passcheck(password_otp?.value)) {
                //   setPassclear(true)
                // }
                // console.log("pass", passcheck(password_otp?.value))
              }}
              placeholder={'Password...'}
              required={true}
              onKeyPress={handleKeyPress}
            />
          </div>

          <div className="login_button_container submit-btns">
            {/* {passclear && ( */}
            <div
              className="emailsubbutton shadow"
              onClick={() => {
                emailLogin()
              }}
            >
              <div className="buttontext">Sign In</div>
            </div>
            {/* )} */}
            <div
              className="emailsubbutton shadow"
              style={{ backgroundColor: colourObj.color_15 }}
              onClick={() => {
                setEmail_signup(true)
              }}
            >
              <div className="buttontext">Create Account</div>
            </div>
          </div>

          <div className="clickme"
            onClick={() => {
              logout()
              history.replace('/')
            }}
            style={{ color: colourObj.color_m1 }}
          >Having trouble logging in? <u>CLICK HERE</u></div>

          <Cookie googlecookies={google_cookies} />

        </div>
      )}
      {
        email_signup &&
        <SignupEmailPopup />
      }
    </>
  )
}

export default LoginEmailPopup