import React from 'react'
import { useColourObjectStore } from '../../../../store/ColourStore'
import { useLanguageStore } from '../../../../store/LanguageStore'

const LoaderPopup = (props: any) => {
  const { colourObj } = useColourObjectStore()
  const { Language, setLanguage } = useLanguageStore();

  return (
    <div className='reward_container'>
      {(Language?.value === "KANNADA") ?
        <>
          <div className='reward_text' style={{ color: colourObj.color_m1 }}>
          ದಯಮಾಡಿ ನಿರೀಕ್ಷಿಸಿ!
            <br />
            <br />
            ನಿಮ್ಮ ಉತ್ತರವನ್ನು ಸಲ್ಲಿಸಲಾಗುತ್ತಿದೆ...
            {/* ಮುಂದುವರೆಯಲು ಕೆಳಗಿನ ಟಿಕ್ ಮಾರ್ಕ್ ಅನ್ನು ಟ್ಯಾಪ್ ಮಾಡಿ. */}
          </div></>
        :
        <>
          <div className='reward_text' style={{ color: colourObj.color_m1 }}>
            Please wait!
            <br />
            <br />
            Your answer is being submitted...
          </div>
        </>
      }
    </div>
  )
}

export default LoaderPopup
