import React, { useEffect, useState } from 'react'
import './style.css'
import { newUserSignup } from '../../../../utils/actions'
import { useAuth } from '../../../../store/authStore';
import { userstore } from '../../../../store/userStore';
import Cookie from 'components/Common/Cookies';
import { useHistory, useLocation } from 'react-router-dom';
// import { userActiveLevelDetailsStore } from '../../../../store/ActiveLevelDetailsStore';
import { useColourObjectStore } from '../../../../store/ColourStore';
import McInput from 'components/Common/McInput';
// import { useActiveScreen } from '../../../../store/ActiveScreen';
// import { useActivePopup } from '../../../../store/ActivePopup';
import LoginEmailPopup from '../LoginEmailPopup';

const SignupEmailPopup = (props: any) => {
  const history = useHistory()
  const { colourObj } = useColourObjectStore();
  // const { setActiveScreen } = useActiveScreen();
  // const { setActivePopup } = useActivePopup();
  const { auth, setAuth, logout } = useAuth()
  const { setselfuser } = userstore()
  const [google_cookies, setgoogle_cookies] = useState('')
  const [firstname, setfirstname] = useState('')
  const [lastname, setlastname] = useState('')
  const [dob, setdob] = useState()
  const [phoneno, setphoneno] = useState('')
  const [username, setusername] = useState('')
  const [pppp, setpppp] = useState(false)
  const [email_signup, setEmail_signup] = useState(false)
  // const search = useLocation().search;
  // const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();

  const handleSignup = () => {
    let a: any = document.getElementById("firstname_data").value
    let b: any = document.getElementById("lastname_data").value
    let c: any = document.getElementById("dob_data").value
    let d: any = document.getElementById("phoneno_data").value
    let e: any = document.getElementById("username_data").value
    let f: any = document.getElementById("email_data").value
    let g: any = document.getElementById("conpassword_data").value
    let data = {
      firstname: a,
      lastname: b,
      dob: c,
      phone: d,
      username: e,
      email: f,
      password: g,
      auth_type: "MC",
      user_type: "NORMAL",
      is_active: true,
    }

    if (g === password_otp) {
      newUserSignup(loginCallback, auth, data)
    } else {
      console.log("Response: Match password with confirm password");
      // passHandler()
    }
  }

  const loginCallback = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      console.log('response ' + JSON.stringify(data));
      localStorage.setItem('AuthToken', JSON.stringify(data.data.token));
      localStorage.setItem('UserDetails', JSON.stringify(data.data.user_details));
      localStorage.setItem('UsedSocialLoginMethod', "true");
      setAuth(data.data.token)
      setselfuser(data.data.user_details)
      setEmail_signup(true)

      // const id = new URLSearchParams(search).get('id');

      // if (id) {
      //   loadActiveleveldetailsbyqr(id)
      //   setActiveScreen("login")
      // }
      // else {
      //   setActiveScreen("Home")
      // }

      // setActivePopup("")
    } else {
      console.log('error ' + JSON.stringify(data));
      console.log('error:: ' + JSON.stringify(errorresponse));
    };
  }

  const passHandler = () => {
    if (password_otp === conpassword_otp) {
      setpppp(false)
    } else {
      setpppp(true)
    }
  }

  // const dobHandler = () => {
  //   let d: any = document.getElementById("dob_data")
  //   let r: any = /^(0?[1-9]|[12][0-9]|3[01])[\/\-](0?[1-9]|1[012])[\/\-]\d{4}$/

  //   let c: any = r.test(d)
  //   setdob(c)
  // }

  interface object1 {
    value: any
    error: any
  }
  const [username_email_or_phone, setusername_email_or_phone] =
    useState<object1>({
      value: '',
      error: false,
    })
  // const [password_otp, setpassword_otp] = useState({
  //   value: '',
  //   error: false,
  // })

  // const [conpassword_otp, setConpassword_otp] = useState({
  //   value: '',
  //   error: false,
  // })
  const [password_otp, setpassword_otp] = useState('')

  const [conpassword_otp, setConpassword_otp] = useState('')

  return (
    <>
      {!email_signup && (
        <div className='popupformcontainer'>
          <div className="login_button_container">
            <McInput
              label={'FIRST NAME'}
              id={'firstname_data'}
              type={'text'}
              value={firstname?.value}
              onChange={setfirstname}
              required={true}
            />
            <div className='mandate-icon'>*</div>
          </div>
          <div className="login_button_container">
            <McInput
              label={'LAST NAME'}
              id={'lastname_data'}
              type={'text'}
              value={lastname?.value}
              onChange={setlastname}
              required={true}
            />
            <div className='mandate-icon'>*</div>
          </div>
          <div className="login_button_container">
            <McInput
              label={'DOB'}
              id={'dob_data'}
              type={'text'}
              value={dob}
              onchange={setdob}
              required={true}
            />
          </div>
          <div className="login_button_container">
            <McInput
              label={'PHONE NO'}
              id={'phoneno_data'}
              type={'text'}
              value={phoneno}
              onchange={setphoneno}
              required={true}
            />
          </div>
          <div className="login_button_container">
            <McInput
              label={'USERNAME'}
              id={'username_data'}
              type={'text'}
              required={true}
              value={username}
              onchange={setusername}
            />
            <div className='mandate-icon'>*</div>
          </div>
          <div className="login_button_container">
            <McInput
              label={'EMAIL'}
              id={'email_data'}
              type={'text'}
              inputtype={'email'}
              value={username_email_or_phone?.value}
              onchange={(val: any) => {
                setusername_email_or_phone({
                  value: val,
                  error: '',
                })
              }}
            />
            <div className='mandate-icon'>*</div>
          </div>
          <div className="login_button_container">
            <McInput
              label={'PASSWORD'}
              id={'password_data'}
              type={'password'}
              max_length="8"
              required={true}
              value={password_otp}
              onchange={setpassword_otp}
            />
            <div className='mandate-icon'>*</div>
          </div>
          <div className="login_button_container">
            <McInput
              label={'CONFIRM PASSWORD'}
              id={'conpassword_data'}
              inputtype='confpassword'
              type={'password'}
              passconfirm={password_otp === conpassword_otp ? false : true}
              max_length="8"
              required={false}
              value={conpassword_otp}
              onchange={setConpassword_otp}
            />
            <div className='mandate-icon'>*</div>
          </div>
          <div className="login_button_container submit-btns">
            <div
              className="emailsubbutton shadow"
              onClick={() => {
                if (password_otp === conpassword_otp) {
                  handleSignup()
                }
              }}
            >
              <div className="buttontext">Submit</div>
            </div>
            <div
              className="emailsubbutton shadow"
              style={{ backgroundColor: colourObj.color_15 }}
              onClick={() => {
                setEmail_signup(true)
              }}
            >
              <div className="buttontext">Sign In</div>
            </div>
          </div>

          <div className="clickme"
            onClick={() => {
              logout()
              history.replace('/')
            }}
            style={{ color: colourObj.color_m1 }}
          >Having trouble logging in? <u>CLICK HERE</u></div>

          <Cookie googlecookies={google_cookies} />
        </div>
      )}

      {
        email_signup &&
        <LoginEmailPopup />
      }
    </>
  )
}

export default SignupEmailPopup